<template>
  <div>
    <div class="main">
      <h2>Каледарь событий</h2>
      <p>Выберай дату:
        <input v-model="date" type="date" class="">
      </p>
      <input v-model="name" placeholder="Введите событие">
      <p>
        <button @click="addEvent">Добавить</button>
      </p>
    </div>

    <div class="events">
      <div v-for="(event, index) in events" :key="index" class="event">
        <p>{{ event.name }}</p>
        <p> {{ event.date }}</p>
      </div>
    </div>
  </div>

</template>

<script>
export default {
  props: {},

  comments: {},
  data() {
    return {
      date: new Date().toISOString().split('T')[0],
      name: '',
      events: []
    }
  },
  created() {
  },
  mounted() {
    this.loadEvent();
  },
  methods: {
    addEvent() {
      console.log(this.date);
      console.log(this.name);
      const event = {
        date: this.date,
        name: this.name,
      }

      this.events.push(event)
      this.saveEvents()
      console.log(this.events);
    },
    saveEvents() {
      localStorage.setItem('events',  JSON.stringify(this.events))
    },
    loadEvent() {
      this.events = JSON.parse(localStorage.getItem('events')) || []
    }
  }

}
</script>

<style lang="scss" scoped>
.main, button, input {
  font-size: 25px;
  text-align: center;
  font-family: Brush Script MT, Brush Script Std, cursive;
}
input{
  width: 220px;
}
button:hover {
  transition-duration: 0.4ms;
  background-color: #4CAF50;
  color: white;
}
input[type=text]:focus {
  transition: width 0.4s ease-in-out;
  width: 300%;
}
.events {
  display: flex;
  justify-content: center;
  align-items: center;
  flex-direction: column;
}
.event {
  font-size: 25px;
  border:5px #00B344  ridge;
  margin-top: 40px;
  width: 35%;
}
</style>